@import 'variables';

.form-input {
  padding: 8px 12px;
  border: 1px solid $gray-300;
  border-radius: $border-radius-md;
  font-size: $font-sm;
  outline: none;

  &:focus { border-color: $brand-brown; }
}

.form-select {
  @extend .form-input;
  min-width: 180px;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin-bottom: $spacing-md;

  label {
    font-size: $font-sm;
    font-weight: 600;
    color: $gray-700;
  }

  input[type="text"],
  textarea,
  select {
    padding: 8px 12px;
    border: 1px solid $gray-300;
    border-radius: $border-radius-md;
    font-size: $font-sm;
    font-family: inherit;
    box-sizing: border-box;

    &:focus {
      outline: none;
      border-color: $brand-brown;
    }
  }
}

@media (max-width: $bp-mobile-max) {
  .form-input {
    font-size: $font-base;
    padding: 10px 12px;
    min-height: $touch-secondary;
  }

  .form-select {
    min-width: 0;
    width: 100%;
    font-size: $font-base;
    min-height: $touch-secondary;
  }

  .form-group {
    input[type="text"],
    textarea,
    select {
      font-size: $font-base;
      padding: 10px 12px;
      min-height: $touch-secondary;
    }
  }
}
